/* Agent card glow effects */
.agent-active {
  box-shadow: 0 0 20px rgba(16, 185, 129, 0.1);
}

.agent-idle {
  opacity: 0.7;
}

/* Priority badges */
.priority-p0 {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

