  color: #3b82f6;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

/* Scanline effect for header */
.scanline::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 2px,
    rgba(16, 185, 129, 0.03) 2px,
    rgba(16, 185, 129, 0.03) 4px
  );
  pointer-events: none;
}

